.home-wrapper {
  height:100vh;
  width: 80vw;
  max-width: 320px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

#lines {
  position: absolute;
  z-index: -1;
  stroke-width: 3;
}

.title-block {
  width: 60vw;
  background-color: #fefefe;
  height: 60vw;
  max-width: 130px;
  max-height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title-block .button {
  margin-bottom: 8px;
}

path {
  stroke-width: 2px;
  stroke: darkgray;
  fill: transparent;
}

svg {
  max-width: 350px;
  max-height: 350px;
  height: 350px;
  margin-top: 10vh;
}